body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.main {
	position: relative;
	width: 100%;
	/* height: 200%; */
	color: #fff8de;
	/* overflow: hidden; */
}

span.header {
	font-family: "Josefin Sans", sans-serif;
	font-weight: 700;
	position: absolute;
	display: inline-block;
	width: 500px;
	-webkit-transform: translate3d(0, -50%, 0);
	        transform: translate3d(0, -50%, 0);
	font-size: 9em;
	line-height: 0.9em;
	pointer-events: none;
	top: 350px;
	left: 50px;
}

img.header-left {
	font-family: "Josefin Sans", sans-serif;
	font-weight: 700;
	display: inline-block;
	-webkit-transform: translate3d(0, -50%, 0);
	        transform: translate3d(0, -50%, 0);
	line-height: 1em;
	font-size: 3.2em;
	overflow: visible;
	text-align: center;
	width: 15rem;
	padding-bottom: 5rem;
}

div.header-small {
	font-family: "DIN Condensed", sans-serif;
	font-size: 2.5rem;
	position: absolute;
	top: 0;
	left: 0;
	width: 58%;
	height: 70%;
	display: flex;
	text-align: center;
	align-items: center;
	justify-content: center;
}

div.header-major {
	font-family: "Palm Canyon Drive", sans-serif;
	font-size: 0.3rem;
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	display: flex;
	text-align: center;
	align-items: center;
	justify-content: center;
}

div.header-major > span {
	font-size: 30em;
}

@media only screen and (max-width: 1000px) {
	div.header-major > span {
		font-size: 20em;
	}
}

@media only screen and (max-width: 800px) {
	div.header-major > span {
		font-size: 15em;
	}
	span.header {
		top: 200px;
		left: 60px;
		font-size: 4em;
		width: 200px;
	}
	a.bottom-left {
		display: none;
	}
}

@media only screen and (max-width: 600px) {
	div.header-major > span {
		font-size: 10em;
	}
}

a {
	font-family: "Josefin Sans", sans-serif;
	font-weight: 400;
	font-size: 18px;
	color: inherit;
	position: absolute;
	display: inline;
	text-decoration: none;
	z-index: 1;
}

a.top-left {
	top: 60px;
	left: 60px;
}

a.top-right {
	top: 60px;
	right: 60px;
}

a.bottom-left {
	bottom: 60px;
	left: 60px;
}

a.bottom-right {
	bottom: 60px;
	right: 60px;
}

canvas {
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
	overflow: hidden;
}

.grid {
	display: flex;
	flex-wrap: wrap;
}

.grid .item {
	position: relative;
	width: 50vw;
	height: 50vw;
	background: #eee;
}

@media only screen and (max-width: 480px) {
	.grid .item {
		width: 100vw;
		height: 100vw;
	}
}

.scroll-container {
	position: absolute;
	overflow: auto;
	top: 0px;
	width: 100%;
	height: 100vh;
	font-size: 20em;
	font-weight: 800;
	line-height: 0.9em;
}

.burger-menu,
.burger-menu.open {
	margin-top: 1.5rem;
	overflow: visible;
	display: inline-block;
	cursor: pointer;
	position: fixed;
	right: 20px;
	top: 8px;
	z-index: 9999;
	background: #fff;
	padding: 10px;
	border-radius: 25px;
	box-shadow: 0 1px 4px rgba(0, 0, 0, 0.3);
}

.burger-menu .bar1,
.bar2,
.bar3 {
	width: 25px;
	height: 3px;
	background-color: #333;
	margin: 4px 0;
	-webkit-transition: 0.6s;
	transition: 0.6s;
}

.burger-menu.open .bar1 {
	-webkit-transform: rotate(-45deg) translate(-4px, 4px);
	transform: rotate(-45deg) translate(-4px, 4px);
}

.burger-menu.open .bar2 {
	opacity: 0;
}

.burger-menu.open .bar3 {
	-webkit-transform: rotate(45deg) translate(-6px, -6px);
	transform: rotate(45deg) translate(-6px, -6px);
}

/* Menu */

.menu {
	width: 100%;
	display: block;
	text-align: center;
	padding: 0px;
}

.menu ul {
	position: relative;
	top: 0px;
	font-size: 24px;
	padding: 0px;
}
.menu li {
	list-style: outside none none;
	margin: 10px 0px;
	padding: 0;
	cursor: pointer;
}
.menu li:hover {
	color: #ff5757;
}

* {
  box-sizing: border-box; }

html,
body {
  height: 100vh;
  padding: 0;
  margin: 0;
  font-family: "avenir next", avenir, -apple-system, BlinkMacSystemFont, "helvetica neue", helvetica, ubuntu, roboto, noto, "segoe ui", arial, sans-serif;
  color: #374047; }

#root {
  display: flex;
  flex-direction: column;
  height: 100vh; }

body {
  display: flex;
  flex-direction: column;
  height: 100vh; }

.main {
  display: flex;
  flex-direction: column;
  flex: 1 0 auto;
  width: 100%;
  height: 100vh;
  background: -webkit-gradient(linear, left top, left bottom, from(#feb4b4), to(#ff5e5e));
  background: linear-gradient(180deg, #feb4b4, #ff5e5e 100%); }

.info {
  font-family: "DIN Condensed";
  position: relative;
  z-index: 1;
  letter-spacing: 3px;
  color: white;
  text-align: center;
  margin-bottom: 3rem;
  font-weight: 700; }
  .info a {
    color: inherit; }
  .info .notice {
    margin-top: 1rem;
    font-weight: 100; }
  @media screen and (min-width: 980px) {
    .info {
      font-size: 18px; }
      .info .notice {
        display: none; } }

.hero {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  justify-content: space-between;
  background: -webkit-gradient(linear, left top, left bottom, from(#ff5e5e), to(#feb4b4));
  background: linear-gradient(180deg, #ff5e5e, #feb4b4 100%); }

.hero-body {
  display: flex;
  flex-grow: 1;
  flex-shrink: 0;
  padding: 3rem 1.5rem; }
  @media screen and (min-width: 980px) {
    .hero-body {
      padding: 8rem 1.5rem; } }

.container {
  max-width: 1200px;
  width: 100%;
  margin: 0 auto; }

.row {
  display: flex;
  flex-wrap: wrap; }

.column {
  display: flex;
  flex: 1 1 auto;
  padding: 10px;
  width: 100%; }
  @media screen and (min-width: 980px) {
    .column {
      width: 33.33333333333333%; } }

.card {
  display: flex;
  flex-direction: column;
  text-align: center;
  padding: 40px;
  background-color: #fff;
  box-shadow: 0px 10px 30px -5px rgba(0, 0, 0, 0.3);
  -webkit-transition: box-shadow 0.5s;
  transition: box-shadow 0.5s;
  will-change: transform; }

.card div:hover {
  color: #ff5757; }

.card-title {
  font-family: "DIN Condensed";
  color: black;
  font-size: 40px;
  font-weight: 600; }

.card-body {
  font-family: "DIN Condensed";
  color: black;
  margin-top: 27px;
  margin-bottom: 27px;
  line-height: 1.5; }
  @media screen and (min-width: 576px) {
    .card-body {
      font-size: 25px; } }

.image-container {
  margin-top: auto;
  padding-top: 27px;
  padding-bottom: 18px; }
  @media screen and (min-width: 576px) {
    .image-container {
      padding-bottom: 30px; } }
  .image-container img {
    width: 100%; }

.image-inner-container {
  width: 100%;
  margin: 0 auto;
  max-width: 250px; }

.ratio {
  position: relative;
  width: 100%;
  height: 0;
  overflow: hidden; }

.ratio-inner {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0; }

.carousel {
	justify-content: center;
	margin-top: 5%;
}

.script-bf-box {
	display: flex;
	width: 300px;
	height: 400px;
	background: black;
	border-radius: 10%;
	border: 4px solid black;
	margin: 10px;
}

